.gutneify--getting-started--wrapper{
	.gutenify-header-left{
		> .fs-notice{
			display: none!important;
		}

	}

}
#gutenify-admin-about-page #message {
    display: none;
}
#gutenify-admin-about-page {
	margin: 20px;
    max-width: 100%;
}
#gutenify-admin-about-page .subscribe-panel i {
    color: #10b510;
    margin-right: 15px;
    display: inline-block;
    padding-top: 9px;
}
.gutenify-about-container {
    padding: 40px 60px;
    border-bottom: 1px solid #ddd;
    border-radius: 2px;
    margin: 30px 0;
    background: #fff;
    box-shadow: 0 0 50px #dddddd7d;
}
a.gutenify-admin-button.premium-button {
    background: #34b03b;
    margin-left: 5px;
}
h3.gutenify-admin-card-title {
    font-size: 1.4rem!important;
    text-transform: capitalize;
}
a.gutenify-admin-button {
    padding: 10px 30px;
    text-decoration: inherit;
    background: #0060f3;
    color: #fff;
    border-radius: 50px;
    margin-top: 20px;
    display: inline-block;
    line-height: 1.75;
    font-size: 16px;
}
a.gutenify-admin-button.primary.large {
    background: #e68836;
    margin-right: 9px;
}


.feature-list {
    display: grid;
    grid-gap: 50px;
    grid-template-columns: 3fr 1fr;
    grid-template-rows: 1fr;
    max-width: 100%;
}

.gutenify-about-container.gutenify-about-content {
    margin: 0;
}
.gutenify-options-features > li {
    /* font-size: 14px; */
    line-height: 1.8;
    display: inline-flex;
    align-items: center;
    color: #262222;
    width: 50%;
    float: left;
    margin-bottom: 15px;
    font-weight: 500;
}


.gutenify-options-features>li img {
    margin-right:5px;
}
.gutenify-options-features {
    list-style: none;
    padding: 0;
    margin: 0 0 30px;
}

.gutenify-admin-card-title.is-sub {
    border-bottom: 1px solid #ebf2f5;
    font-size: 2rem;
    text-transform: capitalize;
    margin-top: 0;
    color: #000;
}

.gutenify-about-side .gutenify-admin-card {
    background-color: #fff;
    padding: 50px;
    margin-bottom: 30px;
    background-repeat: no-repeat;
    background-position: 100% 100%;
    /* border-radius: 15px; */
    border: 1px solid #ebf2f5;
    background: linear-gradient(135deg, #0dcad2, #0060f3);
    color: #fff;
}
.gutenify-about-side span i {
    color: #ffca2d;
}
h4.gutenify-admin-card-title.is-small {
    color: #fff;
}
.gutenify-about-side .gutenify-admin-card a.gutenify-admin-button.large.gutenify-block.is-default {
    background: #fff;
    color: #000;
}
.gutenify-admin-card.cart-two {
    background: #fff;
    color: #000;
    margin: 0;
}
#gutenify-admin-about-page .gutenify-about-side .gutenify-admin-card h3 {
    font-size: 1.5rem;
    color: #fff;
}
#gutenify-admin-about-page  .gutenify-admin-card.cart-two h3 {
    color: #010101;
}
#gutenify-admin-about-page div#setting-error-tgmpa {
    display: none;
}
card.cart-two h3 {
    color: #000;
}
#gutenify-admin-about-page  .admin-grid-2 {
    display: grid;
    grid-gap: 4%;
    grid-template-columns: 48% 48%;
    grid-template-rows: 1fr;
    max-width: 100%;
