}
#gutenify-admin-about-page  .admin-grid-3{
    display: grid;
    grid-gap: 50px;
    grid-template-columns: 2fr 1fr;
    grid-template-rows: 1fr;
    max-width: 100%;
}
#subcription-email {
    width: 500px;
    /* justify-content: center; */
    display: flex;
    padding: 10px;
    border: 1px solid #f0f0f1;
    border-radius: 50px;
    position: relative;
    display: grid;
    /* grid-gap: 50px; */
    grid-template-columns: 2fr 1fr;
    grid-template-rows: 1fr;
    max-width: 100%;
    box-shadow: 0 0 23px #0000001a;
    margin: 22px auto;
}
button.wp-block-search__button.has-icon {
    background: #2960f3;
    color: #fff;
    border-radius: 50px;
    border: none;
    cursor: pointer;
}
#subcription-email input[type="text"] {
    border: none;
    padding: 10px 25px;
    background: #f1f1f1;
    border-radius: 50px;
}

#subcription-email input[type="text"] {
    border: none;
    padding: 10px 15px;
    background: #fff;
    border-radius: 50px;
}
.gutenify-header-left {
    text-align: center;
}
.gutenify-header-left h2 {
    font-size: 3rem;
    text-transform: capitalize;
}
.gutenify-header-left.admin-col {
    text-align: left;
    color: #fff;
}

#gutenify-admin-about-page h2 {
    font-size: 2.5rem;
    line-height: 1.5;
    margin: 0;
    color: #fff;
}
#gutenify-admin-about-page h3 {
    font-size: 2rem;
    line-height: 1.5;
    margin-bottom: 15px;
}
#gutenify-admin-about-page, #gutenify-admin-about-page p {
    color: #838383;
    font-size: 17px;
    line-height: 1.7;
}
#gutenify-admin-about-page p {
    color: #838383;
    font-size: 17px;
    line-height: 1.7;
    margin: 0 0 15px;
}
#gutenify-admin-about-page .gutenify-header-left.admin-col p {
    color: #fff;
}
#gutenify-admin-about-page img {
    max-width: 100%;
    vertical-align: middle;
}
.admin-col > img {
    box-shadow: 0 15px 24px #0000001c;
}
.gutenify-about-container.gutenify-admin-card.gutenify-admin-card-header {
	background: linear-gradient(45deg, #34b03b, #3aa04070);
    justify-content: center;
    align-items: center;
}

.gutenify-card-header-button-group {
    text-align: center;
}
a.gutenify-admin-button:hover, button.wp-block-search__button.has-icon:hover, a.gutenify-admin-button.primary.large:hover {
    background: #4caf50;
}
a.gutenify-admin-button.premium-button:hover {
    background: #2960f3;
}
.btn {
    display: inline-block;
    font-weight: 400;
    color: #212529;
    text-align: center;
    vertical-align: middle;
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    background-color: transparent;
    border: 1px solid transparent;
    padding: 0.375rem 0.75rem;
    font-size: 1rem;
    line-height: 1.5;
    border-radius: 0.25rem;
    transition: color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,box-shadow .15s ease-in-out;
}

.btn-play:before {
    content: "";
    top: -20px;
    left: -20px;
    right: -20px;
    bottom: -20px;
    background-color: #0298e2;
    opacity: .5;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%,-50%);
    transform: translate(-50%,-50%);
    border-radius: 100px;
    z-index: -1;
    -webkit-animation-name: pulseTwo;
    animation-name: pulseTwo;
    -webkit-animation-duration: 2s;
    animation-duration: 2s;
    -webkit-animation-delay: .5s;
    animation-delay: .5s;
    -webkit-animation-timing-function: linear;
    animation-timing-function: linear;
    -webkit-animation-iteration-count: infinite;
    animation-iteration-count: infinite;
    -webkit-animation-direction: normal;
    animation-direction: normal;
}
.btn-play {
    border-radius: 50%;
    background-color: #0298e2;
    width: 75px;
    height: 75px;
    padding-left: 27px;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%,-50%);
    transform: translate(-50%,-50%);
    -webkit-animation-name: pulseButton;
    animation-name: pulseButton;
    -webkit-animation-duration: 2s;
    animation-duration: 2s;
    -webkit-animation-timing-function: linear;
    animation-timing-function: linear;
    -webkit-animation-iteration-count: infinite;
    animation-iteration-count: infinite;
    -webkit-animation-direction: normal;
    animation-direction: normal;
}
.btn-play img {
    width: 80%!important;
    /* width: 100%; */
}
.admin-col {
    position: relative;
}
@keyframes playButtonShadowMobile {
    0% {
        width: 100%;
        height: 100%;
    }
    50% {
        width: calc(100% + 40px);
        height: calc(100% + 40px);
    }
    to {
        opacity: 0;
        width: calc(100% + 40px);
        height: calc(100% + 40px);
    }
}
@keyframes pulseButton {
    0% {
        -webkit-box-shadow: 0;
        box-shadow: 0;
    }
    40% {
        -webkit-box-shadow: 0 0 0 20px #0297e262;
        box-shadow: 0 0 0 20px #0297e262;
    }
    to {
        -webkit-box-shadow: 0 0 0 25px rgba(251, 31, 64, 0);
        box-shadow: 0 0 0 25px rgba(251, 31, 64, 0);
    }
}


.gutneify--getting-started--video-wrapper{
	min-width: 600px;
	video {
		width: 100%;
	}
}


@media screen and (max-width: 782px) {
	#gutenify-admin-about-page  .admin-grid-3,#gutenify-admin-about-page  .admin-grid-2 {
		grid-template-columns: 1fr;

	}
	.gutenify-template-kit-header-left.admin-col {
		text-align: center;
	}
	.gutenify-template-kit-header-left h2 {
		font-size: 2rem;

	}
	.feature-list {
		grid-template-columns: 1fr;

	}
	#gutenify-admin-about-page,#setting-error-tgmpa {
		margin: 20px;
	}
	.gutenify-template-kit-options-features > li {
		width: 100%;
	}
}
